import React, { Suspense } from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';

import { store } from './store';

const dynamicFederation = async (scope, module) => {
  const container = window[scope]; // or get the container somewhere else
  // Initialize the container, it may provide shared modules
  await container.init(__webpack_share_scopes__.default);
  return container.get(module).then(factory => {
    const Module = factory();
    return Module;
  });
};

const Counter = React.lazy(() => dynamicFederation('count', './Counter'));
const Chirps = React.lazy(() => dynamicFederation('chirper', './Chirps'));

const App = () => {
  return (
    <Provider store={store}>

      <AppBar position="static">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            News
          </Typography>
        </Toolbar>
      </AppBar>
      <Box>
        Welcome to Chirper
        <Box>
          <Suspense fallback="Loading...">
            <RemoteApp store={store} />
          </Suspense>
        </Box>
      </Box>
    </Provider>
  );
};

ReactDOM.render(<App />, document.getElementById('root'));
